body, html {
  margin: 0;
  padding: 0;
}

.container {
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__hand {
  transform-origin: 50% 100%;
  position: absolute;
  top: 50%;
  left: 50%;
}

.stopwatch__hand--minute {
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite minutes;
  transform: translate(-50%, -100%)rotate(90deg);
}

.stopwatch__hand--minute:after {
  content: "";
  z-index: 4;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  display: block;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.stopwatch__hand--second {
  z-index: -1;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite seconds;
}

.stopwatch--speed-up .stopwatch__hand--minute {
  animation-duration: 600s;
}

.stopwatch--speed-up .stopwatch__hand--second {
  animation-duration: 10s;
}

@keyframes minutes {
  0% {
    transform: translate(-50%, -100%)rotate(0);
  }

  100% {
    transform: translate(-50%, -100%)rotate(360deg);
  }
}

@keyframes seconds {
  0% {
    transform: translate(-50%, -100%)rotate(0);
  }

  100% {
    transform: translate(-50%, -100%)rotate(360deg);
  }
}
